</template>

<script>
    import ResponsiveImage from "@/components/ResponsiveImage.vue";

    export default {
        components: {
            ResponsiveImage
        },

        data() {
            return {
                isAlipayModalActive: false,
                isWeChatPayModalActive: false,
                patrons: [],
                isLoadingPatrons: true,
                sortField: "valueInUsd",
                sortOrder: "desc",
                defaultSortOrder: "desc"
            };
        },

        mounted() {
            this.load();
        },

        methods: {
            openAlipayDialog(){
                this.isAlipayModalActive = true;
                this.$gtag.event('How-to-donate', {'event_category': 'Clicks', 'event_label': 'Alipay show'});
            },
            openWeChatPayDialog(){
                this.isWeChatPayModalActive = true;
                this.$gtag.event('How-to-donate', {'event_category': 'Clicks', 'event_label': 'WeChatPay show'});
            },
            load() {
                if (this.patrons.length > 0) {
                    this.isLoadingPatrons = false;
                } else {
                    this.fallbackLoad();
                }
            },
            fallbackLoad(){
                this.$http.get(`https://www.coodesker.com/coodesker-desktop/patrons.json`)
                    .then(async res => {
                        this.treatDataFallback(await res.json());
                    }, error => {
                        console.log("Exception in getting patrons, in fallback.", error);

                        this.setDefaultPatrons();

                        this.$gtag.exception({'description': error, 'fatal': true});
                    })
                    .catch(e => {
                        console.log("Exception in getting releases, in fallback.", e);

                        this.setDefaultPatrons();

                        this.$gtag.exception({'description': e, 'fatal': true});
                    });
            },
            treatDataFallback(data) {
                this.patrons = [];
                this.patrons = data;
                this.isLoadingPatrons = false;
            },
            setDefaultPatrons() {
                this.isLoadingPatrons = false;
            },
            sortName(a, b, isAsc){
                return ('' + a.name).localeCompare(b.name) * (isAsc ? -1 : 1);
            },
            sortValue(a, b, isAsc){
                return (a.valueInUsd > b.valueInUsd ? 1 : -1) * (isAsc ? 1 : -1);
            },
            openLink(row){
                if (row.url.length > 0) {
                    window.open(row.url, '_blank', 'noopener');
                    this.$gtag.event('Patron table', {'event_category': 'Clicks', 'event_label': 'Installer ' + row.urlPretty });
                }
            }
        }
    };
</script>

